	module.exports = {"data":{"markdownRemark":{"html":"<h2>What is an affinity diagram?</h2>\n<p>An affinity diagram is used to organize information into like groups/categories to identify common themes and patterns. The creation of affinity diagrams allows the team to quickly identify trends in the data to facilitate creating accurate business and user requirements.</p>\n<h2>How to create an affinity diagram:</h2>\n<p>Start by picking out key phrases, words, and other useful parts of the interview and then organize them into like groups by writing them on sticky notes. If you are part of a remote team, you could use a free online tool like <a href=\"https://trello.com/\">Trello</a>.</p>\n<p>After the initial sticky note session is complete, organize the results into a report that can be shared. This can be done in Excel or Trello.</p>","frontmatter":{"title":"Affinity Diagram","overview":[{"heading":"Investment","amount":1,"description":"Once an interview is complete, documenting the key responses is quick and easy."},{"heading":"Validity","amount":3,"description":"Affinity diagrams can be somewhat subjective, as the creator makes determinations about what was important and how these items are grouped."},{"heading":"Expertise","amount":1,"description":"Creating an affinity diagram requires no special tools or methods and can be done by anyone on the team with access to recordings of the interviews."}],"resources":null,"relatedMethods":[{"text":"Empathy Mapping","url":"/empathy-map/"},{"text":"Personas","url":"/persona/"},{"text":"Interviews","url":"/interview/"}]},"fields":{"slug":"/affinity-diagram/"}},"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"title":"Usability Testing","url":"/usability-testing/"}}},{"node":{"frontmatter":{"title":"User Research Guidelines","url":null}}},{"node":{"frontmatter":{"title":"A/B Test","url":"/ab-test/"}}},{"node":{"frontmatter":{"title":"Affinity Diagram","url":"/affinity-diagram/"}}},{"node":{"frontmatter":{"title":"Card Sort","url":"/card-sort/"}}},{"node":{"frontmatter":{"title":"Empathy Map","url":"/empathy-map/"}}},{"node":{"frontmatter":{"title":"Focus Group","url":"/focus-group/"}}},{"node":{"frontmatter":{"title":"Interview","url":"/interview/"}}},{"node":{"frontmatter":{"title":"Journey Map","url":"/journey-map/"}}},{"node":{"frontmatter":{"title":"Participatory Design","url":"/participatory-design/"}}},{"node":{"frontmatter":{"title":"Persona","url":"/persona/"}}}]}},"pathContext":{"slug":"/affinity-diagram/"}}

